@import url(https://fonts.googleapis.com/css2?family=Cairo&display=swap);
body{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
body{display:flex;justify-content:center;font-family:"Cairo",Roboto,Arial;background-color:#fff}.progress,.progress-bar{border-radius:50rem !important;height:2rem !important}.card{background-color:transparent;flex:3 1 300px;height:200px;-webkit-perspective:1000px;perspective:1000px;-webkit-user-select:none;-ms-user-select:none;user-select:none;margin-bottom:1rem}.card .inner{position:relative;width:100%;height:100%;text-align:center;transition:-webkit-transform .8s;transition:transform .8s;transition:transform .8s, -webkit-transform .8s;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.card .front,.card .back{font-size:2rem;border-radius:10px;position:absolute;width:100%;height:100%;display:flex;align-items:center;justify-content:center;-webkit-backface-visibility:hidden;backface-visibility:hidden}.card .front .sound,.card .back .sound{position:absolute;width:4rem;height:4rem;top:0rem;right:0rem;display:flex;justify-content:center;align-items:center}.card .front .sound svg,.card .back .sound svg{width:2rem}.card .front{background-color:#bbb;color:#000}.card .back{background-color:#1e90ff;color:#fff;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.new-word-btn{width:6rem;height:3rem;font-size:12px;color:#fff;background-color:#1e90ff;border:none;-webkit-user-select:none;-ms-user-select:none;user-select:none;border-radius:10px;box-shadow:0px 2px #000;transition:100ms ease;margin:0 1rem}.new-word-btn:active{box-shadow:0px 1px #000;-webkit-transform:translateY(1px);transform:translateY(1px)}.new-word-btn:active,.new-word-btn:hover{-webkit-filter:brightness(0.9);filter:brightness(0.9)}.flippedCard{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.vase{position:fixed;width:10vw;bottom:0}a{text-decoration:none}
.burger-menu{position:fixed;top:0;height:100vh;background-color:#eee;width:200px;left:-200px;transition:.5s ease}.burger-menu .hamburger-icon{position:fixed;left:0}.open-menu{left:0}
