$menu-width: 200px;
$menu-openning-duration: 0.5s;
$menu-animation-curve: ease;

.burger-menu {
    position: fixed;
    top: 0;
    height: 100vh;
    background-color: #eee;
    width: $menu-width;
    left: -$menu-width;
    transition: $menu-openning-duration $menu-animation-curve;
    .hamburger-icon {
        position: fixed;
        left: 0;
    }
}

.open-menu {
    left: 0;
}